* {
    margin: 0;
    padding: 0;
}

.navbar {
    width: 100%;
    height: 65px;
    display: flex;
    position: sticky;
    z-index: 110;
    top: 0;
    background: linear-gradient(#778fff, #753aff, #7682ea) 110px 0px;
    color: whitesmoke;
    align-items: center;
}

body {
    background-color: aliceblue;
}

.navbar .nav-icon {
    text-align: center;
    font-family: inter;
    cursor: pointer;
    font-size: .8em;
    background-color: transparent;
    margin: 0 1em 0 1em;
    padding: .4em .8em;
    user-select: none;
    border: 1px solid transparent;
    transition: .1s linear;
    border-radius: .4em;
}

.navbar svg {
    transition: .1s linear;
    height: 20px;
    width: 20px;
}

.navbar .nav-icon:hover {
    background-color: #19191a31;
    background-color: #cbe0ff47
}

.navbar .nav-icon p {
    font-size: 1em;
}

.navbar .nav-icon:active {
    border: 1px solid #cccccca0;
}

.search-nav {
    width: 50%;
    display: flex;
    align-items: center;
    height: 45px;
    margin-left: 1em;
    margin-right: 1em;
}

.search-nav input {
    width: 100%;
    height: 40px;
    text-indent: 1em;
    transition: .1s linear;
    outline: none;
    background-color: #edf8fc75;
    border: 1px solid transparent;
    border-radius: .4em;
    color: azure;
    font-family: roboto;
    font-size: 1em;
}

.search-nav input::-webkit-search-cancel-button {
    appearance: none;
    background-image: url("./Icons/red-x.svg");
    width: 20px;
    background-size: contain;
    background-color: aliceblue;
    position: relative;
    right: 10px;
    cursor: pointer;
    border-radius: 10px;
    height: 20px;
}

.search-nav input::placeholder {
    transition: .1s linear;
    font-family: roboto;
    color: #f1f9f99d;
}

.search-nav input:focus {
    border-color: #fffcfc;
}


.search-nav input:focus::placeholder {
    color: aliceblue;
}

.image-container {
    display: flex;
    box-sizing: border-box;
    flex-wrap: wrap;
    justify-content: center;
}

.image {
    transition: .1s linear;
    padding: 1em .4em 1em .4em;
    box-shadow: 1px 0 5px #19191a31;
    height: 500px;
    margin: 1em;
    cursor: pointer;
}

.image img {
    height: 400px;
    transition: .1s linear;
}

.image:hover img {
    opacity: 80%;
}

.image:hover {
    box-shadow: 1px 0 10px #19191a31;
}

.main-image {
    width: 100%;
}

.properties {
    padding: .4em 1em;
    font-family: inter, roboto, sans-serif;
}

button {
    position: relative;
    overflow: hidden;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

span.ripple {
    position: absolute;
    border-radius: 52%;
    transform: scale(0);
    animation: ripple 500ms linear;
    background-color: rgba(255, 255, 255, 0.7);
}

@keyframes ripple {
    to {
        transform: scale(4);
        opacity: 0;
    }
}

.offline {
    text-align: center;
    margin-top: 4em;
    color: #19191a31;
    font-family: roboto;
}

.btn-offline {
    transition: .1s linear;
    margin-top: 1em;
    padding: .4em 2em;
    background-color: transparent;
    border: 1px solid #19191a31;
    font-family: inherit;
    color: #139df4;
    justify-self: center;
    cursor: pointer;
    font-size: 1.1em;
    align-items: center;
}

.btn-offline:hover {
    background-color: #e3f2fd;
}

.loader {
    text-align: center;
    margin: 1em 0;
}

.loader img {
    width: 50px;
}

.download,
.search-nav button {
    transition: .1s linear all;
    display: flex;
    align-items: center;
    padding: .5em 1em;
    margin: .5em 0;
    font-family: inter;
    background-color: #a9b3bd69;
    border: 1px solid transparent;
}

.search-nav button {
    margin: 0 1em;
    cursor: pointer;
    padding: .91em 2em;
    border-radius: .41em;
}

.search-nav button:hover {
    background-color: #2c2d2f55;
    color: whitesmoke;
}

.download:hover {
    background-color: #97a0aa80;
}

.download svg,
.download img {
    width: 20px;
    margin: 0 0.5em;
    height: 20px;
}

.btn-offline img {
    width: 20px;
    transition: .1s linear;
    margin: -3px 11.5px;
}

.f-inter {
    font-family: inter, roboto, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.text-center {
    text-align: center;
}

.my-1 {
    margin-top: 1em;
    margin-bottom: 1em;
}

.justify-center {
    justify-content: center;
}

.flex {
    display: flex;
}

.brand-name {
    margin: 1em 0;
    text-align: center;
    font-family: roboto;
}

.brand-name span {
    color: #7847e9;
}

.c-white {
    color: white;
}

@media screen and (min-width:320px) and (max-width:620px) {

    .search-nav button {
        margin: 0 .5em;
        justify-content: center;
        font-size: .6em;
    }

    .navbar .nav-icon {
        margin: 0 .2em;
    }

    .search-nav {
        /* margin: 0 .2em; */
        height: 33px;
        margin-left: 0;
        margin-right: 0;
    }

    .search-nav input {
        height: 33px;
    }

    .nav-icon h2 {
        font-size: .8em;
    }

    .search-nav {
        width: 70%;
    }
}